//Global mixins for seasons
@mixin learn-more-btn {
  box-shadow: 7px 8px $shadow;
  color: white !important;
  display: inline-block;
  font-family: $font-comic;
  font-size: 26px;
  text-decoration: none !important;
  text-transform: uppercase;
  transform: translate(0, 0);
  transition: all 0.3s ease;
  padding: 5px 30px;
  position: relative;
  will-change: transform;
}
@mixin learn-more-btn-before {
  content: " ";
  position: absolute;
  z-index: -1;
  top: -6px;
  left: -6px;
  right: -6px;
  bottom: -6px;
}
@mixin learn-more-btn-hover {
  box-shadow: 2px 4px $shadow;
  transform: translate(2px, 2px);
}

.future-events {
  padding: 0;
  position: relative;
}

.month {
  color: white;
  font-family: $font-family-sans-serif;
  font-size: 28px;
  font-weight: lighter;
  text-transform: uppercase;
}

.day {
  color: white;
  font-family: $font-family-serif;
  font-size: 48px;
  font-weight: bold;
}

// Entomology ($aqua)
.entomology {
  h1 {
    color: $aqua;
  }

  h2 {
    color: $aqua-dark;
  }

  .entry-content .l8 {
    a {
      color: $aqua;

      &:hover {
        background-color: $aqua-light;
        color: $aqua-dark;
      }

      &:active, &:focus {
        background-color: $aqua;
        color: $white;
      }
    }
  }

  .card.scientist, &.card.scientist {
    @include color-card($aqua-light, $aqua, $aqua-dark);

    a {
      color: $navy;
      text-shadow: none;
    }
  }

  .learn-more-btn {
    @include learn-more-btn;

    background-color: $aqua;
    border: 6px solid $aqua-light;
    text-shadow: 4px 2px $aqua-dark;

    &::before {
      @include learn-more-btn-before;

      border: 2px solid $aqua-dark;
    }

    &:hover {
      @include learn-more-btn-hover;

      background-color: $aqua-light;
      border: 6px solid $aqua;
    }

    &:active {
      background-color: $aqua;
      border: 6px solid $aqua-dark;
      box-shadow: 0 0;
    }
  }

  &.event {
    background: $aqua;

    .date {
      background-color: $aqua;
      border: 6px solid $aqua-light;

      &::before {
        border: 2px solid $aqua-dark;
      }
    }

    .month {
      text-shadow: 4px 2px $aqua-dark;
    }

    .day {
      text-shadow: 4px 2px $aqua-dark;
    }

    .title {
      border-top: 9px solid $aqua-light;
      background-color: $aqua;
    }
  }
}

.tribe-events-category-entomology, .taxonomy-tribe_events_cat-entomology-data {
  .tribe-event-url {
    color: $aqua;
  }

  h1.tribe-events-single-event-title, h2.tribe-events-page-title {
    color: $aqua;
  }

  #tribe-events .tribe-events-button {
    background-color: $aqua-dark;

    &:hover {
      background-color: $aqua-light;
      color: $aqua-dark;
    }

    &:active, &:focus {
      background-color: $aqua;
      color: $white;
    }
  }
}

