  height: 3px;
  background: #c2c0c2;
  border: none;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 14px;
  width: 14px;
  border-radius: 50%;
  background-color: #308593;
  transform-origin: 50% 50%;
  margin: -5px 0 0 0;
  transition: .3s;
}

