ol.form-progress {
  list-style: none;
  display: table;
  margin: 1.5rem 0;
  table-layout: fixed;
  width: 100%;
  padding: 0;
  counter-reset: progressStep;
}

@media screen and (max-width: 767px) {
  ol.form-progress {
    padding: 0;
  }
}

ol.form-progress:focus {
  outline: 0;
}

ol.form-progress:focus li[data-step-current] {
  outline: #2F2F2F dotted 1px;
}

ol.form-progress li {
  text-transform: uppercase;
  text-align: center;
  display: table-cell;
  list-style: none;
  margin: 0;
  padding: 2rem 0 0;
  position: relative;
  width: 100%;
  counter-increment: progressStep;
}

ol.form-progress li[data-step-current] {
  color: #308593;
  font-weight: bold;
}

ol.form-progress li::before,
ol.form-progress li::after {
  background: #CFF3FF;
  color: #000349;
  display: block;
  position: absolute;
}

ol.form-progress li::before {
  content: counter(progressStep);
  border-radius: 3px;
  font-weight: bold;
  height: 1.5rem;
  left: 50%;
  margin-left: -0.75rem;
  line-height: 1.5rem;
  padding-left: 0.1rem;
  top: 0;
  transform: scale(1, 1);
  width: 1.5rem;
  z-index: 1;
}

ol.form-progress li[data-step-current]::before {
  background: #308593;
  color: #FFF;
}

ol.form-progress li[data-step-complete]::before {
  background: #4CBED8;
  color: #FFF;
}

ol.form-progress li::after {
  content: '';
  height: 0.25rem;
  left: -50%;
  top: 0.65rem;
  width: 100%;
}

ol.form-progress li[data-step-complete]::after,
ol.form-progress li[data-step-current]::after {
  background: #CFF3FF;
}

